.jumbotron-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 430px;
  background-image: url('../images/background.jpg');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;

  h1 {
    margin-bottom: 15px;
  }

  button {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #eee;
    width: 100px;
    height: 50px;
    border-radius: 5px;
  }

  button:hover {
    background-color: rgb(30, 151, 199);
  }
}