.addButton {
  width: 100px;
  height: 50px;
  background-color: #eee;
  margin-bottom: 50px;

  &:hover {
    background-color: aqua;
  }
}

.visited {
  position: sticky;
  top: 10px;
  width: 200px;
  border: 1px solid grey;
  
  img {
    width: 100%;

    &:hover {
      cursor: pointer;
    }
  }
}

.App {
  text-align: center;
  border-radius: 5px;
  min-width: 1400px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
