.cart-container {
  margin-top: 30px;

  .cart-list-container {
    display: flex;
    flex: 1;
    height: 50px;
  
    .cart-list {
      flex: 1;
      border: 1px solid grey;
      line-height: 50px;;

      button {
        background-color: #eee;
        padding: 7px;
        border-radius: 5px;
        margin-right: 5px;

        &:hover {
          background: green;
        }

        &:last-child:hover {
          background: crimson;
        }
      }
    }
  }

  .payment-container {
    margin: 25px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .payment {
      
    }

    .pay {
      background-color: #09aa5c;
      width: 170px;
      height: 60px;
      font-size: 18px;
      font-weight: bold;
      color: white;
      margin: 15px 0;

      &.zeroPay {
        background-color: grey;
        cursor: not-allowed;
      }
    }
  }

  .grey {
    position: fixed;
    top: 0px;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(238, 238, 238, 0.534);

    .payment-modal-container {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 70px;
      width: 50%;
      height: 80%;
      background-color: white;
      z-index: 1;
      border: 1px solid rgb(223, 217, 217);
      border-radius: 10px;

      .payment-modal-btn-container {

        .modal-btn {
          background-color: #09aa5c;
          width: 170px;
          height: 60px;
          font-size: 18px;
          font-weight: bold;
          color: white;
          margin: 15px;
          border-radius: 15px;
        }
      }
    }
  }

  .goHome {
    background-color: #09aa5c;
    width: 170px;
    height: 60px;
    font-size: 18px;
    font-weight: bold;
    color: white;
    margin: 15px;
    border-radius: 15px;
  }

}