.product-container {
  width: 1200px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 0 auto;

  .product {
    width: 350px;
    margin-bottom: 30px;
    padding: 5px;

    &:hover {
      cursor: pointer;
      box-shadow: 0px 0px 10px 1px gray;
      border-radius: 5px;
    }

    img {
      width: 100%;
    }
  }
}