input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.detail-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1400px;
  margin: 0 auto;

  .detail-description {
    margin-bottom: 20px;
  }

  .orderBtn {
    width: 100px;
    height: 50px;
    background-color: #eee;
    margin: 5px;
    border-radius: 5px;

    &:hover {
      background-color: aqua;
    }
  }


}

.quanButtonContainer {
  display: flex;
  margin: 10px 0;
  color: black;
  font-weight: bold;

  .numberInputBox {
    display: flex;
    padding-left: 18px;
    width: 46px;
    height: 34px;
    border: 1px solid #eee;
  }

  .quanBtn {
    background-color: #eee;
    margin: 0;
    width: 34px;
    height: 34px;
    border-radius: 0;
    font-size: 16px;
  }
}