.nav {
  background-color: #eee;
  display: flex;
  align-items: center;

  .logo {
    a{
      font-weight: 700;
      padding: 10px;
    }
  }

  .nav-item-container {
    display: flex;
    
    .nav-item {
      a {
        display: inline-block;
        padding: 10px;
        transition: background-color .5s;

        &:hover {
          background-color: coral;
        }
      }
    }
  }
}